.wrapper {
   height: 100vh;
   width: 100%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: #258ea6;
}
.form-wrapper {
   border: 1px solid white;
   width: 400px;
   display: flex;
   background-color: rgb(53, 167, 187);
   flex-direction: column;
   border-radius: 10px;
   padding: 20px 40px;
   box-shadow: 3px 6px 10px rgba(4, 53, 68, 0.746);
}
form {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
}
h1 {
   text-align: center;
   width: 100%;
   color: #111;
   font-weight: lighter;
}
label {
   font-size: 0.8em;
   margin-bottom: 0.25em;
   color: #222;
   font-weight: lighter;
}
input {
   padding: 10px 10px;
   border-radius: 5px;
   outline: none;
   border : 1px solid #cfcfcf;
} 
input::placeholder {
   font-size: 1.2em;
   font-weight: lighter;
   color: #999;
}
input.error {
   border: 1px solid red;
}
.errorMessage {
   color: red;
   font-size: 0.75em;
   display: relative;
}
.firstname {
   margin-right: 1%;
}
.lastname {
   margin-right: 1%;
}
.firstname,
.lastname,
.email,
.password {
   display: flex;
   flex-direction: column;
   margin-bottom: 15px;
}
.firstname,
.lastname {
   width: 49%;
}
.email,
.password {
   width: 100%;
}
.createAccount {
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   color: white;
}
button {
   background-color: rgb(0, 77, 10);
   width: 100%;
   border: 0;
   height: 2em;
   border-radius: 5px;
   color: white;
   font-weight: bold;
}
button:hover:active {
   background-color: white;
   color: rgb(151, 148, 148);
}